.alfaiataria-body {
  position: absolute;
  height: 385px;
  width: 1015px;
  left: 98px;
  top: 3036px;
  border-radius: 0px;
  z-index: 1;

  border: 1px solid #e5e5e5;
  box-sizing: border-box;
}

.alfaiataria-title {
  position: absolute;
  left: 170px;
  top: 50px;
}

.alfaiataria-title h1 {
  font-family: Cormorant;
  font-size: 62px;
  font-style: normal;
  font-weight: 300;
  line-height: 75px;
  letter-spacing: 0em;
  text-align: left;

  color: #014561;
}

.alfaiataria-sub-title {
  position: absolute;
  left: 189px;
  top: 120px;
}

.alfaiataria-sub-title p {
  font-family: Lato;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 0em;
  text-align: left;

  color: #c4c4c4;
}

.alfaiataria-info {
  position: absolute;
  left: 170px;
  top: 170px;
}

.alfaiataria-info li {
  font-family: Cormorant;
  font-size: 20px;
  line-height: 29px;
  font-style: italic;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: left;

  color: #727272;
}

.alfaiataria-info2 {
  position: absolute;
  left: 440px;
  top: 169px;
}

.alfaiataria-info2 li {
  font-family: Cormorant;
  font-size: 20px;
  line-height: 29px;
  font-style: italic;
  font-weight: 400;
  line-height: 29px;
  letter-spacing: 0em;
  text-align: left;

  color: #727272;
}

.alfaiataria-btnn {
  position: absolute;
  height: 50px;
  width: 330px;
  top: 285px;
  left: 168px;

  background: #014561;
}

.alfaiataria-btnn:hover {
  background-color: #002c3f;
  transition: all ease 0.3s;
}

.alfaiataria-btnn a {
  position: absolute;
  width: 165px;
  height: 22px;
  left: calc(50% - 165px / 2 + 0.5px);
  top: calc(50% - 22px / 2);

  font-family: Lato;
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: 22px;
  letter-spacing: 0em;
  text-align: center;

  color: #e5e5e5;
}

.alfaiataria-image-linha {
  position: absolute;
  height: 464px;
  width: 550px;
  left: 658px;
  top: -40px;
  border-radius: 0px;
  

  border: 1px solid #1c86a4;
  background-color: transparent;
  box-sizing: border-box;
}

.alfaiataria-image img {
  position: absolute;
  left: 8px;
  top: -10px;
  z-index: -1;
}
